.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  text-align: center;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.link {
  text-decoration: none;
  color: lightblue;
}

@media screen and (max-width: 600px) {
  .App-header {
    text-align: center;
    padding-left: 2%;
    padding-right: 2%;
  }
}